.ticker_5 {
    .ticker__item {
        right: 100%;
        width: 50%;
        animation: going2 15s infinite linear;

        &:nth-child(2) {
            animation-delay: 5s;
        }

        &:nth-child(3) {
            animation-delay: 10s;
        }

        &:nth-child(4) {
            animation-delay: 15s;
        }

        @media(max-width:768px) {
            width: 90%;
        }

        @media (max-width: 991px) {
            font-size: rem(16);
        }

        @keyframes going2 {
            from {
                transform: translateX(300%);
            }

            to {
                transform: translateX(0%);
            }
        }
    }
}